/* Chat */

.bubbles {
	overflow-y: auto;

	.bubble {
		position: relative;
		margin: 0 1em 2em 1em;
		padding: 1em;
		background: #e5e5ea;
		border-radius: var(--border-radius);
		color: #000;

		&.remote, &.local {
			&:before {
				content: '';
				position: absolute;
				bottom: 2px;
				height: 20px;
				transform: translate(0, -2px);
			}
			&:after {
				content: '';
				position: absolute;
				bottom: 2px;
				width: 26px;
				height: 20px;
				background: #fcfcfc;
				transform: translate(-30px, -2px);
			}
		}

		&.remote {
			margin: 0 1em 2em 0;
			&:before {
				inset-inline-start: -7px;
				border-inline-start: 20px solid #e5e5ea;
				border-end-start-radius: 0;
				border-end-end-radius: 16px 14px;
			}
			&:after {
				bottom: 2px;
				inset-inline-start: 4px;
				border-end-end-radius: 10px;
			}
		}

		&.local {
			margin: 0 12px 2em 1em;
			background: #0b93f6;
			color: #fff;
			&:before {
				inset-inline-end: -7px;
				border-inline-end: 20px solid #0b93f6;
				border-end-start-radius: 16px 14px;
			}
			&:after {
				inset-inline-end: -56px;
				border-end-start-radius: 10px;
			}
		}

		&.event {
			background: transparent;
			border: 1px dashed #ccc;
			color: inherit;
		}

		/* Colors */

		&.me, &.staff {

			&.local:after {
				border-color: #0b93f6;
			}
		}

		&.customer:after {
			content: "";
			position: absolute;
			bottom: 0;
			width: var(--gutter-x);
			height: var(--gutter-y);
		}

		.text {
		}

		textarea {
			height: 4em;
			padding: 0;
			box-sizing: border-box;
			color: inherit;
			background: transparent;
			border: none;
			outline: none;
			box-shadow: none;
		}

		.date {
			padding-top: 0.5em;
			font-size: 0.8em;
			text-align: center;
			opacity: 0.5;
		}

		.remove {
			position: absolute;
			top: 0.5em;
			inset-inline-end: 1em;
			color: inherit;
		}
	}
}
